import { writable } from 'svelte/store';
import './rolls.gql';
import { api } from '../core';
export const stage = writable(null);
export const rolls = writable(null);
export const init = async (newStage) => {
    // const { data } = await api('/graphql', {
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json'
    //   },
    //   body: JSON.stringify({ query, variables: { stage: newStage } })
    // });
    const { data } = await api(`/data/${newStage}.rolls.json`);
    rolls.set(data.rolls);
};
